@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,800);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    margin: 0;
    padding: 0;
  }
  
  .header {
    padding: 20px
  }
  
  nav {
    width: 100%;
    margin: 0;
    top: 0;
    background: #212121;
    color: #ccc;
  }
  
  .navul {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
  }
  
  .navList {
    height: 100%;
    flex: 1 1;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    -webkit-transition: all 0.3s ease-in;
    transition: all 0.3s ease-in;
    cursor: pointer;
    color: white;
    text-decoration: none;
  }
  
  .navList:hover {
    background: #848484;
    color: #212121;
    text-decoration: none;
  }
  
  .fixed-nav {
    position: fixed;
  }
  
  .content {
    width: 80%;
    margin: 0 auto;
    text-align: justify;
    padding-top: 20px;
  }
  
  .content > div {
    margin: 10px 0;
  }
.Upage{
    margin: auto;
    margin-top: 15%;
    margin-bottom: 15%;
} 



.center{
    justify-content: center;
    margin: auto;
    align-content: center;
    text-align: center;
    width: 500px;
}

.contentCon{
  height: 100vh;
  /* background-image: linear-gradient(to bottom right, #656FC4,#656FC4, #9096c0, #656FC4); */
  background-color:#656FC4;
}
.fileInput{
  border-bottom: 1px solid lightgray;
  border-right: 1px solid lightgray;
  border-top: 1px solid lightgray;
  border-left: 1px solid lightgray;
  padding: 10px;
  margin: 15px;
  width: 50%;
  min-width: 200px;
  cursor: pointer}
  
.imgPreview{
  text-align: center;
  margin: auto;
  height: 200px;
}

.img{
    width: 100%;
    margin: auto;
    background-color: rgb(212, 212, 212);
    border: 3px solid #efefef;
    border-radius: 3px;
    height: 500px;
    object-fit: contain;
  
}

  
.previewText{
  width: 100%;
  margin-top: 20px;
}
.submitButton{
  padding: 12px;
  margin-left: 10px;
  background: white;
  border: 1px solid lightgray;
  border-radius: 3px;
  font-weight: 700;
  font-size: 10pt;
  cursor: pointer;}
.submitButton:hover{
    background: #efefef
}
.centerText{
  text-align: center;
  width: 50%}


.App {
  text-align: center;
}

.App-logo {
  pointer-events: none;
  height: 500px;
}


.App-header::before{
  opacity: 0.7;
}


.App-link {
  color: #61dafb;
}

