html, body {
  font-family: Open Sans;
  font-size: 18px;
  line-height: 28px;
  scroll-behavior: smooth;
}

h1 {
  letter-spacing: -15px;
  font-family: Open Sans;
  color: white;
  text-align: center;
  font-size: 200px;
  font-weight: 800;
  line-height: 200px;
}

h3 {
  letter-spacing: 0px;
  font-family: Open Sans;
  color: white;
  text-align: center;
  font-size: 30px;
  font-weight: 800;
  line-height: 200px;
}

.MainContainer {
  -webkit-perspective: 1px;
          perspective: 1px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  height: 100vh;
  width: 100vw;
  overflow-x: hidden;
  overflow-y: scroll;
}

.ContentContainer {
  position: relative;
  margin: auto;
  display: block;
  justify-content: center;
  align-items: center;
  /* background-image: linear-gradient(to bottom right, #656FC4,#656FC4, #9096c0, #656FC4); */
  height: 100vh;
}

.Content {
  max-width: 750px;
  margin: auto;
  padding: 75px 0;
}

p {
  margin: 75px 0;
}

.ParallaxContainer {
  display: flex;
  flex: 1 0 auto;
  position: relative;
  z-index: -1;
  height: 100vh;
  justify-content: center;
  align-items: center;
  -webkit-transform: translateZ(-0.99px) scale(2);
          transform: translateZ(-0.99px) scale(2);
  background-image: url(https://www.belizehub.com/wp-content/uploads/2016/06/birds-of-belize.jpg);
  background-color: rgb(250,228, 216);
  background-size: cover;
}

