body {
    margin: 0;
    padding: 0;
  }
  
  .header {
    padding: 20px
  }
  
  nav {
    width: 100%;
    margin: 0;
    top: 0;
    background: #212121;
    color: #ccc;
  }
  
  .navul {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
  }
  
  .navList {
    height: 100%;
    flex: 1;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    transition: all 0.3s ease-in;
    cursor: pointer;
    color: white;
    text-decoration: none;
  }
  
  .navList:hover {
    background: #848484;
    color: #212121;
    text-decoration: none;
  }
  
  .fixed-nav {
    position: fixed;
  }
  
  .content {
    width: 80%;
    margin: 0 auto;
    text-align: justify;
    padding-top: 20px;
  }
  
  .content > div {
    margin: 10px 0;
  }